.App {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
    font-size: calc(10px + 1.1vmin);
    width: 100%;
    background-color: hsl(0, 0%, 83%);
}

.App-container-1 {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: 100vh;
}

.App-container-2 {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 100vh;
    /* display: none; */
}

.App-logo {
    -webkit-flex: 3 1;
            flex: 3 1;
    padding-left: 2rem;
    padding-top: 0.5em;
}

.container2-header {
    -webkit-flex: 3 1;
            flex: 3 1;
    width: 100%;
}

.container2-content {
    /* border: 1px solid blue; */
    -webkit-flex: 7 1;
            flex: 7 1;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    width: 100%;
}

.container2-filler {
    -webkit-flex: 1 1;
            flex: 1 1;
}

.container2-footer {
    -webkit-flex: 2 1;
            flex: 2 1;
}

.App-logo div {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    position: relative;
    font-size: 1.5em;
}

.App-logo .separator {
    color: white;
    font-size: 1.7em;
    padding-left: 0.2em;
    padding-right: 0.2em;
}

.App-logo h4 {
    font-weight: 400;
}

.App-header {
    background-color: #282c34;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    font-size: calc(10px + 2vmin);
    color: white;
}

.App-link {
    color: #61dafb;
}

@media (min-width: 700px) {
    .App {
        -webkit-flex-direction: row;
                flex-direction: row;
        /* flex-direction: row-reverse; */
    }

    .App-container-1 {
        -webkit-flex: 4 1;
                flex: 4 1;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
    }

    .App-container-2 {
        -webkit-flex: 3 1;
                flex: 3 1;
    }

    .container2-filler {
        -webkit-flex: 1 1;
                flex: 1 1;
        display: none;
    }

    .container2-footer {
        -webkit-flex: 1 1;
                flex: 1 1;
        display: none;
    }
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

